<template>
  <div class="join-container">

    <div id="join-bg">
      <nav-bar></nav-bar>
      <div class="join-form" id="join-from">
        <!---->
        <div class="join-title">
          
        </div>
        <!---->
        <div class="input">
          <mu-text-field v-model="username" type="text" hint-text=""
          name="username" icon="account_circle" />
          <br/>
          <mu-text-field v-model="email" type="email" hint-text="" icon="email" name="email"/>
          <br/>
          <mu-text-field v-model="password" type="password" hint-text="" icon="remove_red_eye"
                         name="password"/>
          <br/>
          <mu-text-field v-model="secondPassword" type="password" hint-text="" icon="remove_red_eye"
                         name="secondPassword"/>
          <br/>
          <div align="center">
            <!---->
            <img v-bind:src="captchaBase64Img" @click="SetBase64Img" height="60px" width="240px"/>
          </div>
          <mu-text-field v-model="captcha" type="text" hint-text=""
                         name="captcha" icon="keyboard"/>
          <br/>
          <br/>

          <!---->
          <div style="width: 88px;margin:0px auto;">
            <mu-raised-button class="login-button" label="" @click="register"/>
          </div>
          <br/>
        </div>
      </div>
      <my-footer></my-footer>
    </div>
    <!--  -->
    <div>
      <mu-dialog :open="registerDialog" title="">
        {{registerInformation}}
        <mu-flat-button label="" slot="actions" primary @click="registerDialogClose"/>
      </mu-dialog>
    </div>
  </div>
</template>

<script>
import NavBar from "./Navber";
import MyFooter from "./Footer";

export default {
  name: "Join",
  components: {
    NavBar,
    MyFooter
  },
  data() {
    return {
      captchaBase64Img: "",
      username: "",
      email: "",
      password: "",
      secondPassword: "",
      captcha: "",
      registerInformation: "",
      registerDialog: false,
      status: -100,
    };
  },
  mounted() {
    this.bgAndFooterLayout();
    this.SetBase64Img();
  },
  methods: {
    bgAndFooterLayout: function() {
      // noinspection UnnecessaryLocalVariableJS
      var height = parseInt(window.innerHeight);
      // noinspection JSValidateTypes
      var panelHeight =
        parseInt(document.getElementById("join-from").clientHeight) + 162;
      // noinspection JSValidateTypes
      if (panelHeight > height) {
        document.getElementById("join-bg").style.height =
          panelHeight + 50 + "px";
      } else {
        document.getElementById("join-bg").style.height = height + "px";
      }
    },
    // 
    SetBase64Img: function() {
      let captchaBase64Url = this.GetServerUrl() + "/captcha";
      this.axios
        .get(captchaBase64Url)
        .then((response) => {
          this.captchaBase64Img = response.data;
        })
        .catch((error) => {
          alert(error);
        });
    },
    // 
    register: function() {
      
      // 
      if(this.username.length == 0 ||
         this.username.length >= 30) {
        if (this.username.length == 0) {
          this.registerInformation = "";
        } else if (this.username.length >= 30) {
          this.registerInformation = "30";
        }
        this.registerDialog = true;
        return;
      }
      // email
      if(!this.checkEmail(this.email)) {
        this.registerInformation = "";
        this.registerDialog = true;
        return
      }
      // 
      if(this.password.length == 0 ||
         this.username.password >= 30) {
        if (this.password.length == 0) {
          this.registerInformation = ""
        } else if(this.username.password >= 30) {
          this.registerInformation = "30";
        }
        this.registerDialog = true;
        return;
      }
      // 
      if (this.password != this.secondPassword) {
        this.registerInformation = "";
        this.registerDialog = true;
        return
      }
      // 
      if (this.captcha.length == 0) {
        this.registerInformation = "";
        this.registerDialog = true;
        return
      }

      // post 
      let registerUrl = this.GetServerUrl() + "/user/signup";
      var querystring = require('querystring');
      let params = {
        username: this.username,
        password: this.password,
        email: this.email,
        captcha: this.captcha
      };
      this.axios.post(registerUrl, querystring.stringify(params)).then(response => {
        if (response.data.status == 0) { // 
          this.registerInformation = ",";
        }
        else {
          this.registerInformation = response.data.desc;
        }
        this.status = response.data.status;
        this.registerDialog = true;
      });
    },
    // 
    registerDialogClose: function () {
      this.registerDialog = false;
      if (this.status == 0) { //  
        this.$router.push({path: '/login'});
      } else {
        this.SetBase64Img()
      }
    },
    // email
    checkEmail: function (email) {
      const regex = /[0-9a-zA-Z]*@[0-9a-zA-Z]*\.[0-9a-zA-Z]*/gm;
      const str = email;
      let m;
      let ret = false;
      while ((m = regex.exec(str)) !== null) {
          // This is necessary to avoid infinite loops with zero-width matches
          if (m.index === regex.lastIndex) {
              regex.lastIndex++;
          }
          
          // The result can be accessed through the `m`-variable.
          m.forEach((match, groupIndex) => {
            console.log(match == email);
              if(match == email) {
                ret = true;
              }
          });
      }
      return ret;
    }
  }
};
</script>
<style scoped>
body {
  background-color: #e8e8e8;
}

#join-bg {
  background-color: #e8e8e8;
  min-height: 100%;
}

.join-form {
  width: 400px;
  margin: 100px auto 5%;
  background-color: white;
  border-radius: 10px;
}

.join-title {
  /*border: 2px solid black;*/
  margin: auto 0px;
  font-size: 200%;
  text-align: center;
  height: 57px;
  padding-top: 15px;
}

.input {
  width: 80%;
  margin: 0 auto;
}
.demo-flat-button {
  margin: 12px;
}
</style>



